/**
 * Sidebar styles
 */

.sidebar {

    @include media($mobile, 'max') {
        display: flex;
        flex-direction: column;

        .sidebar__posts {
            order: 1;
            margin-bottom: 1em;
        }

        .headshot {
            order: 2;
        }
    }

    @include media($mobile) {
        width: 25%;
        display: inline-block;
        vertical-align: top;
        padding: 4em 0 0 2em;
        position: relative;
    }

    h2 {
        color: $text-color;

        @include media($mobile) {
            border-bottom: 1px solid $lime-green;
            display: inline-block;
            margin: 0 0 1rem -2.5rem;
            padding: 0 0 .5rem 2.5rem;
        }

        @include media($tablet) {
            font-size: 1.5rem;
        }

    }

    h3 {
        margin-bottom: 0;
    }

    p, a {
        font-size: 1rem;
    }

    a {
        color: $white;

        &:hover {
            color: $lime-green;
        }
    }

    .post-meta {
        display: inline-block;
        float: none;
    }

    .post-link {
        @include media($tablet) {
            font-size: 1rem;
        }
    }
}

.headshot {
    margin-bottom: 1.5em;

    img {
        margin-bottom: 2em;

        @include media($tablet) {
            margin-bottom: 1.5em;
            padding-right: 2em;
        }
    }
}

// Current post
a#current {

    &:before {
        @include media($mobile) {
            content: '';
            position: absolute;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0.75rem 0 0.75rem 0.75rem;
            border-color: transparent transparent transparent $grey-color-medium;
            left: -3%;
            transform: rotate(360deg);
        }
    }
}
